import React from 'react';
import {NavLink} from 'react-router-dom';
import home from './views/images/home.jpg';
import about from './views/images/about.jpg';
import resume from './views/images/resume.png';
import skill from './views/images/skill.jpg';
export const Page=(props)=>{
return(<>
<NavLink to="/" activeClassName="active"><img src={home} alt="Logo" height="30" width="30"/>  Home</NavLink> &ensp;
<NavLink to="/about" activeClassName="active"><img src={about} alt="Logo" height="30" width="30"/>  About Us</NavLink> &ensp;
<NavLink to="/resume" activeClassName="active"><img src={resume} alt="Logo" height="30" width="30"/>  Resume</NavLink>&ensp;
<NavLink to="/skill" activeClassName="active"><img src={skill} alt="Logo" height="30" width="30"/>  Skill</NavLink>
</>
)
}